/* Reset Styles */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}

/* Container Styling */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #fef8e7; /* Light yellow */
}

/* Title */
.title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #000; /* Black */
}

/* Grid Container */
.grid-container {
  position: relative;
  width: 100vw;
  height: 80vh;
}

/* Grid */
.grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, #d3d3d3 1px, transparent 1px),
    linear-gradient(to bottom, #d3d3d3 1px, transparent 1px);
  background-size: 50px 50px;
}

/* Timeline Styling */
.timeline {
  position: absolute;
  top: 50%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.blue-line {
  width: 100%;
  height: 2px;
  background-color: cyan;
}

.future-line {
  width: 100%;
  height: 1px;
  background-color: #d3d3d3; /* Gray */
  position: absolute;
  top: 1px;
}

.events {
  position: absolute;
  width: 100%;
}

.past-event {
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #ffd7d7; /* Light red */
  padding: 5px;
  border-radius: 5px;
}

.future-event {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #ffd7d7; /* Light red */
  padding: 5px;
  border-radius: 5px;
}

.button {
  position: absolute;
  bottom: 20px;
  background-color: cyan;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
}